exports.push([module.id, "\n.action-link[data-v-89c53f18] {\n    cursor: pointer;\n}\n", "", {"version":3,"sources":["/Users/christianbraybrooke/local_sites/b2systems/resources/assets/js/components/passport/resources/assets/js/components/passport/PersonalAccessTokens.vue"],"names":[],"mappings":";AACA;IACA,gBAAA;CACA","file":"PersonalAccessTokens.vue","sourcesContent":["<style scoped>\n    .action-link {\n        cursor: pointer;\n    }\n</style>\n\n<template>\n    <div>\n        <div>\n            <div class=\"card card-default\">\n                <div class=\"card-header\">\n                    <div style=\"display: flex; justify-content: space-between; align-items: center;\">\n                        <span>\n                            Personal Access Tokens\n                        </span>\n\n                        <a class=\"action-link\" tabindex=\"-1\" @click=\"showCreateTokenForm\">\n                            Create New Token\n                        </a>\n                    </div>\n                </div>\n\n                <div class=\"card-body\">\n                    <!-- No Tokens Notice -->\n                    <p class=\"mb-0\" v-if=\"tokens.length === 0\">\n                        You have not created any personal access tokens.\n                    </p>\n\n                    <!-- Personal Access Tokens -->\n                    <table class=\"table table-borderless mb-0\" v-if=\"tokens.length > 0\">\n                        <thead>\n                            <tr>\n                                <th>Name</th>\n                                <th></th>\n                            </tr>\n                        </thead>\n\n                        <tbody>\n                            <tr v-for=\"token in tokens\">\n                                <!-- Client Name -->\n                                <td style=\"vertical-align: middle;\">\n                                    {{ token.name }}\n                                </td>\n\n                                <!-- Delete Button -->\n                                <td style=\"vertical-align: middle;\">\n                                    <a class=\"action-link text-danger\" @click=\"revoke(token)\">\n                                        Delete\n                                    </a>\n                                </td>\n                            </tr>\n                        </tbody>\n                    </table>\n                </div>\n            </div>\n        </div>\n\n        <!-- Create Token Modal -->\n        <div class=\"modal fade\" id=\"modal-create-token\" tabindex=\"-1\" role=\"dialog\">\n            <div class=\"modal-dialog\">\n                <div class=\"modal-content\">\n                    <div class=\"modal-header\">\n                        <h4 class=\"modal-title\">\n                            Create Token\n                        </h4>\n\n                        <button type=\"button\" class=\"close\" data-dismiss=\"modal\" aria-hidden=\"true\">&times;</button>\n                    </div>\n\n                    <div class=\"modal-body\">\n                        <!-- Form Errors -->\n                        <div class=\"alert alert-danger\" v-if=\"form.errors.length > 0\">\n                            <p class=\"mb-0\"><strong>Whoops!</strong> Something went wrong!</p>\n                            <br>\n                            <ul>\n                                <li v-for=\"error in form.errors\">\n                                    {{ error }}\n                                </li>\n                            </ul>\n                        </div>\n\n                        <!-- Create Token Form -->\n                        <form role=\"form\" @submit.prevent=\"store\">\n                            <!-- Name -->\n                            <div class=\"form-group row\">\n                                <label class=\"col-md-4 col-form-label\">Name</label>\n\n                                <div class=\"col-md-6\">\n                                    <input id=\"create-token-name\" type=\"text\" class=\"form-control\" name=\"name\" v-model=\"form.name\">\n                                </div>\n                            </div>\n\n                            <!-- Scopes -->\n                            <div class=\"form-group row\" v-if=\"scopes.length > 0\">\n                                <label class=\"col-md-4 col-form-label\">Scopes</label>\n\n                                <div class=\"col-md-6\">\n                                    <div v-for=\"scope in scopes\">\n                                        <div class=\"checkbox\">\n                                            <label>\n                                                <input type=\"checkbox\"\n                                                    @click=\"toggleScope(scope.id)\"\n                                                    :checked=\"scopeIsAssigned(scope.id)\">\n\n                                                    {{ scope.id }}\n                                            </label>\n                                        </div>\n                                    </div>\n                                </div>\n                            </div>\n                        </form>\n                    </div>\n\n                    <!-- Modal Actions -->\n                    <div class=\"modal-footer\">\n                        <button type=\"button\" class=\"btn btn-secondary\" data-dismiss=\"modal\">Close</button>\n\n                        <button type=\"button\" class=\"btn btn-primary\" @click=\"store\">\n                            Create\n                        </button>\n                    </div>\n                </div>\n            </div>\n        </div>\n\n        <!-- Access Token Modal -->\n        <div class=\"modal fade\" id=\"modal-access-token\" tabindex=\"-1\" role=\"dialog\">\n            <div class=\"modal-dialog\">\n                <div class=\"modal-content\">\n                    <div class=\"modal-header\">\n                        <h4 class=\"modal-title\">\n                            Personal Access Token\n                        </h4>\n\n                        <button type=\"button\" class=\"close\" data-dismiss=\"modal\" aria-hidden=\"true\">&times;</button>\n                    </div>\n\n                    <div class=\"modal-body\">\n                        <p>\n                            Here is your new personal access token. This is the only time it will be shown so don't lose it!\n                            You may now use this token to make API requests.\n                        </p>\n\n                        <textarea class=\"form-control\" rows=\"10\">{{ accessToken }}</textarea>\n                    </div>\n\n                    <!-- Modal Actions -->\n                    <div class=\"modal-footer\">\n                        <button type=\"button\" class=\"btn btn-secondary\" data-dismiss=\"modal\">Close</button>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\n    export default {\n        /*\n         * The component's data.\n         */\n        data() {\n            return {\n                accessToken: null,\n\n                tokens: [],\n                scopes: [],\n\n                form: {\n                    name: '',\n                    scopes: [],\n                    errors: []\n                }\n            };\n        },\n\n        /**\n         * Prepare the component (Vue 1.x).\n         */\n        ready() {\n            this.prepareComponent();\n        },\n\n        /**\n         * Prepare the component (Vue 2.x).\n         */\n        mounted() {\n            this.prepareComponent();\n        },\n\n        methods: {\n            /**\n             * Prepare the component.\n             */\n            prepareComponent() {\n                this.getTokens();\n                this.getScopes();\n\n                $('#modal-create-token').on('shown.bs.modal', () => {\n                    $('#create-token-name').focus();\n                });\n            },\n\n            /**\n             * Get all of the personal access tokens for the user.\n             */\n            getTokens() {\n                axios.get('/oauth/personal-access-tokens')\n                        .then(response => {\n                            this.tokens = response.data;\n                        });\n            },\n\n            /**\n             * Get all of the available scopes.\n             */\n            getScopes() {\n                axios.get('/oauth/scopes')\n                        .then(response => {\n                            this.scopes = response.data;\n                        });\n            },\n\n            /**\n             * Show the form for creating new tokens.\n             */\n            showCreateTokenForm() {\n                $('#modal-create-token').modal('show');\n            },\n\n            /**\n             * Create a new personal access token.\n             */\n            store() {\n                this.accessToken = null;\n\n                this.form.errors = [];\n\n                axios.post('/oauth/personal-access-tokens', this.form)\n                        .then(response => {\n                            this.form.name = '';\n                            this.form.scopes = [];\n                            this.form.errors = [];\n\n                            this.tokens.push(response.data.token);\n\n                            this.showAccessToken(response.data.accessToken);\n                        })\n                        .catch(error => {\n                            if (typeof error.response.data === 'object') {\n                                this.form.errors = _.flatten(_.toArray(error.response.data.errors));\n                            } else {\n                                this.form.errors = ['Something went wrong. Please try again.'];\n                            }\n                        });\n            },\n\n            /**\n             * Toggle the given scope in the list of assigned scopes.\n             */\n            toggleScope(scope) {\n                if (this.scopeIsAssigned(scope)) {\n                    this.form.scopes = _.reject(this.form.scopes, s => s == scope);\n                } else {\n                    this.form.scopes.push(scope);\n                }\n            },\n\n            /**\n             * Determine if the given scope has been assigned to the token.\n             */\n            scopeIsAssigned(scope) {\n                return _.indexOf(this.form.scopes, scope) >= 0;\n            },\n\n            /**\n             * Show the given access token to the user.\n             */\n            showAccessToken(accessToken) {\n                $('#modal-create-token').modal('hide');\n\n                this.accessToken = accessToken;\n\n                $('#modal-access-token').modal('show');\n            },\n\n            /**\n             * Revoke the given token.\n             */\n            revoke(token) {\n                axios.delete('/oauth/personal-access-tokens/' + token.id)\n                        .then(response => {\n                            this.getTokens();\n                        });\n            }\n        }\n    }\n</script>\n"],"sourceRoot":""}]);

